@import "../../../variable.scss";
div.COMMON_INPUT {
    margin: 10px 0;

    input {
        background-color: transparent;
        outline: none;
        border: none;
        width: 92%;
        margin-bottom: 10px;
        font-size: 12px;
        font-family: Roboto;
        color: rgba($color: #ffffff, $alpha: .25);
        letter-spacing: 1px;

        &::placeholder {
            color: rgba($color: #ffffff, $alpha: .25);
            font-family: Roboto;
            letter-spacing: 1px;
        }
    }

    label {
        font-family: Roboto;
        font-size: 13px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1;
        letter-spacing: normal;
        text-align: left;
        margin: 5px 0;
        color: $PRIMARY_COLOR;

    }

    img {
        width: 5% !important;
        margin: 0 !important;
        opacity: .5;
        padding: 2px;
    }

    i {
        cursor: pointer;
    }

    border-bottom: 2px solid rgba($color: #ffffff, $alpha: .25);
}